.simple-info {
  text-align: center;
  position: absolute;
  top: 50%;
  -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
          transform: translateY(-50%);
  width: 100%;
  color: white; }

.simple-info h6 {
  font-weight: 300;
  text-transform: uppercase;
  letter-spacing: 2px; }

.simple-info h3 {
  text-transform: uppercase;
  letter-spacing: 9px; }



