.scroll-down {
  text-align: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  letter-spacing: 12px;
  color: #ffffff;
  -webkit-animation: colorText 5s ease-out infinite, nudgeText 5s ease-out infinite;
          animation: colorText 5s ease-out infinite, nudgeText 5s ease-out infinite; }

@-webkit-keyframes nudgeText {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  20% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  30% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  60% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  80% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  90% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@keyframes nudgeText {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  20% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  30% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  50% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  60% {
    -webkit-transform: translateY(0);
            transform: translateY(0); }
  80% {
    -webkit-transform: translateY(2px);
            transform: translateY(2px); }
  90% {
    -webkit-transform: translateY(0);
            transform: translateY(0); } }

@-webkit-keyframes colorText {
  21% {
    color: #4e5559; }
  30% {
    color: #ffffff; }
  51% {
    color: #4e5559; }
  60% {
    color: #ffffff; }
  81% {
    color: #4e5559; }
  90% {
    color: #ffffff; } }

@keyframes colorText {
  21% {
    color: #4e5559; }
  30% {
    color: #ffffff; }
  51% {
    color: #4e5559; }
  60% {
    color: #ffffff; }
  81% {
    color: #4e5559; }
  90% {
    color: #ffffff; } }



